@font-face {
  font-family: 'helveticaneuecyr_black';
  src: url('fonts/helveticaneuecyr_black.otf') format('opentype');
}
@font-face {
  font-family: 'helveticaneuecyr_roman';
  src: url('fonts/helveticaneuecyr_roman.otf') format('opentype');
}
@font-face {
  font-family: 'helveticaneuecyr_thin';
  src: url('fonts/helveticaneuecyr_thin.otf') format('opentype');
}
body {
  margin: 0;
  font-family: 'helveticaneuecyr_roman', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

