.main__application {
    padding: 25px;
    background-color: #fff;
    width: 100%;
}
@media (max-width: 1199px) {
    .main__application {
        padding: 0;
    }
}
.application__title {
    font-family: 'helveticaneuecyr_black';
    font-size: 20px;
}

.application__text {
    font-size: 18px;
    padding-top: 7px;
}

.application__choice {
    display: flex;
    gap: 30px;
    padding: 28px 0 21px;
}
.choice__item {
    font-size: 16px;
    cursor: pointer;
}
.choice__item_bold {
    font-family: 'helveticaneuecyr_black';
    font-size: 16px;
    text-decoration: underline;
    cursor: pointer;
}