.dropdownMenu {
  margin-bottom: 20px;
}
.dropdownMenuTitle {
  cursor: pointer;
  background: #F6FDFF;
  box-shadow: 0px 6px 10px rgba(219, 219, 219, 0.25);
  border-radius: 5px;
  font-weight: 400;
  font-size: 18px;
  padding: 20px 10px;
  position: relative;
  transition: .3s;
}

.arrow {
  position: absolute;
  right: 15px;
  top: 45%;
  transform: rotate(135deg) translateY(50%);
  width: 8px;
  height: 8px;
  border-top: 2px solid #000000;
  border-right: 2px solid #000000;
  transition: .3s;
}

.arrow__up {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: rotate(315deg) translateY(-50%);
  width: 8px;
  height: 8px;
  border-top: 2px solid #000000;
  border-right: 2px solid #000000;
  transition: .3s;
}

.dropdownMenuItem {
  cursor: pointer;
  margin: 3px 0;
  padding: 10px;
  font-weight: 300;
  background: #FBFDFE;
border-radius: 5px;
font-size: 16px;
position: relative;
}