.form__search {
    display: flex;
    background: #F6FDFF;
    box-shadow: 0px 6px 10px rgba(219, 219, 219, 0.25);
    border-radius: 5px;
    margin: 23px 0 42px;
    padding: 10px 0;
}
.input__search {
    width: 100%;
    color: #8B8B8B;
    background: #F6FDFF;
  border: none;
  border-right: 1px solid #000000;
  padding: 10px 25px;
}

.input__search:focus {
    outline: none;
}
.button {
    border: none;
    cursor: pointer;
    background: #F6FDFF;
    padding: 5px 35px ;
}
