.candidates {
    width: 100%;
    padding: 50px 0 80px;
}

.candidates__container {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

@media (min-width: 1200px) {
    .candidates__container {
        width: 1194px;
    }
}
@media (max-width: 1199px) {
    .candidates__container {
       padding: 0 20px;
    }
}
.title {
    font-family: 'helveticaneuecyr_black';
    font-size: 32px;
}

.blocks {
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
}

.block {
    background: #F6FDFF;
    box-sizing: border-box;
    
    border-radius: 5px;
    padding: 10px 15px;
    width: 370px;
}

.number {
    font-family: 'helveticaneuecyr_black';
    color: #FFEF62;
    font-size: 40px;
}

.block__text {
    padding-top: 5px;
    font-size: 20px;
}

.button {
    width: 370px;
    border: none;
    border-radius: 5px;
    background: #FFEF62;
    height: 100%;
    min-height: 92px;
    font-family: 'helveticaneuecyr_black';
    font-size: 18px;
}