.vacancy {
    width: 100%;
    padding: 50px 0 80px;
    background: #F6FDFF;
}

.vacancy__container {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

@media (min-width: 1200px) {
    .vacancy__container {
        width: 1194px;
    }
}
@media (max-width: 1199px) {
    .vacancy__container {
        padding: 0 20px
    }
}
.title {
    font-family: 'helveticaneuecyr_black';
    font-size: 32px;
}

.text {
    font-size: 20px;
    padding-top: 10px;
}