footer {
    background: #000000;
    color: #FFFFFF;
    width: 100%;
}
.footer__container {
    gap: 40px;
    font-size: 14px;
    margin: 0 auto;
}
@media (min-width: 1200px) {
    .footer__container {
        width: 1194px;
    }
}
@media (max-width: 1199px) {
    .footer__container {
        padding: 0 20px;
    }
}
.footer__text {
    display: flex;
    gap: 15px;
    justify-content: space-between;
    align-items: center;
    padding: 52px 0;
}
@media (max-width: 1199px) {
    .footer__text{
        flex-direction: column;
    }
}
.footer__item {
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.bold {
    font-family: 'helveticaneuecyr_black';
    font-weight: 900;
    font-size: 24px;
    line-height: 23px;
}
.mid__size {
    font-size: 16px;
}
.footer__link {
    color:#FFFFFF;
}

.semi__bold {
    font-weight: 700;
    font-size: 18px;
    position: relative;
}

.arrow {
    position: absolute;
    right: 3px;
    top: 4px;
    transform: rotate(135deg);
    width: 7px;
    height: 7px;
    border-top: 1px solid #FFFFFF;
    border-right: 1px solid #FFFFFF;
}

.mid__size{
    letter-spacing: 0.065em;
    font-family: 'helveticaneuecyr_thin'
}

.link {
    padding: 42px 0;
    border-top: 1px solid #FFFFFF;
    text-align: center;
}