.work {
    width: 100%;
    padding: 50px 0;
}
@media (max-width:768px) {
    .work {
        padding: 20px 0;
    }
}
.work__container {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}
@media (min-width: 1200px) {
    .work__container {
        width: 1194px;
    }
}
@media (max-width: 1199px) {
    .work__container {
        padding: 0 20px;
    }
}
.work__title {
    font-family: 'helveticaneuecyr_black';
    font-size: 32px;
}

.work__text {
    font-size: 20px;
    padding-top: 10px;
}

.button {
    display: flex;
    justify-content: center;
}

.button button {
    cursor: pointer;
    background-color: #FFEF62;
    font-family: 'helveticaneuecyr_black';
    font-size: 14px;
    border-radius: 5px;
    border: none;
    padding: 12px 88px;
}