.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

input[type="file"] {
  display: none
}

.input__name,
.select__value,
.file_upload {
  font-size: 14px;
  box-sizing: border-box;
  color: #8B8B8B;
  background: #FBFDFE;
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  padding: 10px 12px;
  width: 100%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: relative;
}
.input__name:focus,
.select__value:focus {
  outline: none;
}
.select__value {
  background-image: url(./Arrow.svg);
  background-repeat: no-repeat;
  background-position: 95% 50%;
}

.button {
  cursor: pointer;
  background-color: #FFEF62;
  font-family: 'helveticaneuecyr_black';
  font-size: 14px;
  border-radius: 5px;
  border: none;
  padding: 10px 12px;
  width: 100%;
}

.label, .file_upload, .button {
  flex: 1 1 20%;
}