.header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    padding: 30px 0;
    font-size: 14px;
}
@media (min-width: 1200px) {
    .header__container {
        width: 1194px;
    }
}
@media (max-width: 1199px) {
    .header__container {
        display: none;
    }
}
.header__item {
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.bold {
    font-family: 'helveticaneuecyr_black';
    font-weight: 900;
    font-size: 24px;
    line-height: 23px;
}
.mid__size {
    font-size: 16px;
}
.header__link {
    color:#000000;
}

.semi__bold {
    font-weight: 700;
    font-size: 18px;
    position: relative;
}

.arrow {
    position: absolute;
    right: 3px;
    top: 4px;
    transform: rotate(135deg);
    width: 7px;
    height: 7px;
    border-top: 1px solid #000000;
    border-right: 1px solid #000000;
}