.messenger {
    width: 100%;
    padding: 50px 0 80px;
}
@media (max-width: 1199px) {
    .messenger {
        padding: 0 0 15px;
    }
}
.messenger__container {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

@media (min-width: 1200px) {
    .messenger__container {
        width: 1194px;
    }
}
@media (max-width: 1199px) {
    .messenger__container {
        padding: 0 20px;
    }
}
.title {
    font-family: 'helveticaneuecyr_black';
    font-size: 32px;
}

.blocks {
    display: flex;
    padding-top: 25px;
}
@media (max-width: 1199px) {
    .blocks {
        flex-direction: column;
    }
}
.blocks div {
    flex: 1 1 30%;
}


.img {
    margin-top: -30px;
}
@media (max-width: 1199px) {
    .img {
        display: none;
    }
}
.tg {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 25px;
}
@media (max-width: 1199px) {
    .tg {
        align-items: center;
    }
    .tg img {
        width: 60%;

    }
}
@media (max-width: 900px) {
    .tg img {
        width: 100%;
    }
}
.button {
    cursor: pointer;
    background-color: #FFEF62;
    font-family: 'helveticaneuecyr_black';
    font-size: 14px;
    border-radius: 5px;
    border: none;
    padding: 10px 12px;
    width: 100%;
  }