.main__application {
  margin-top: 30px;
  box-sizing: border-box;
  padding: 25px;
  background-color: #fff;
  width: 100%;
}
@media (max-width: 1199px) {
  .main__application {
      padding: 0;
  }
}
.application__title {
  font-family: 'helveticaneuecyr_black';
  font-size: 20px;
}

.application__text {
  font-size: 18px;
  padding-top: 7px;
}
.form {
  margin-top: 20px;
  display: flex;
  gap: 42px;
}
@media (max-width: 1199px) {
  .form {
    flex-direction: column;
    gap: 10px;
  }
}
.input__name,
.select__value {
  font-size: 14px;
  color: #8B8B8B;
  background: #FBFDFE;
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  padding: 10px 0;
  width: 100%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: relative;
}
.input__name:focus,
.select__value:focus {
  outline: none;
}
.select__value {
  background-image: url(./Arrow.svg);
  background-repeat: no-repeat;
  background-position: 95% 50%;
}

.button {
  cursor: pointer;
  background-color: #FFEF62;
  font-family: 'helveticaneuecyr_black';
  font-size: 14px;
  border-radius: 5px;
  border: none;
  padding: 10px 12px;
  width: 100%;
}

.label, .file_upload, .button {
  flex: 1 1 30%;
}