.navbar {
    background-color: #FFEF62;
    width: 100%;
}

.navbar__container {
    margin: 0 auto;
}

@media (min-width: 1200px) {
    .navbar__container {
        width: 1194px;
    }
}

@media (max-width: 1199px) {
    .navbar__container {
        padding: 20px;
    }
}
.navbar__links {
    font-size: 16px;
    display: flex;
    gap: 20px;
    justify-content: space-between;
    padding: 20px 0;
}

@media (max-width: 768px) {
    .navbar__links{
        display: none;
    }
}
.navbar__burger {
    display: none;
}

@media (max-width: 768px) {
    .navbar__burger {
        display: block;
        cursor: pointer;
        text-align: end;
    }

    .navbar__menu {
        display: flex;
        flex-direction: column;
        background-color: #fff;
        position: absolute;
        top: 60px;
        right: 0;
        width: 200px;
        padding: 20px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    }
}