.item {
    display: flex;
    gap: 20px;
    padding: 20px;
    background: #F6FDFF;
    border-radius: 5px;
    font-size: 14px;
    margin-bottom: 20px;
}

.text {
    flex: 1 0 60%;
}

.name {
    font-size: 24px;

}

.payInHour {
    font-weight: 550;
    font-size: 16px;
    padding-top: 20px;
}

.time,
.city,
.house,
.vacancy {
    padding-top: 14px;
}

.questions {
    padding-top: 24px;
    padding-bottom: 5px;
    font-family: 'helveticaneuecyr_black';
}

.buttons {
    display: flex;
    gap: 15px;
}
@media (max-width: 1024px) {
    .buttons{
        flex-direction: column;
    }
}
.buttons select {
    background-color: transparent;
    border: 2px solid #FFEF62;
    border-radius: 5px;
    padding: 8px 14px;
    font-size: 14px;
}
.buttons select:focus {
    outline: none;
}
.button {
    cursor: pointer;
    background-color: #FFEF62;
    font-family: 'helveticaneuecyr_black';
    font-size: 14px;
    border-radius: 5px;
    border: none;
    padding: 8px 14px;
    width: 257px;
}
@media (max-width: 1024px) {
    .button{
        width:auto
    }
}
.picture {
    flex: 1 0 20%
}

.img {
    width: 100%;
    border-radius: 10px;
}
@media (max-width: 1024px) {
    .picture {
        flex: 1 0 40%
    }
}

@media (max-width: 1024px) {
    .picture {
        display: none;
    }
}