.main {
    background-color: #F7FDFF;
    width: 100%;
    padding: 64px 0;
}
@media (max-width: 1199px) {
    .main{
        padding: 25px 0;
    }
}
.main__container {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url(../../assets/main.png);
}
@media (min-width: 1200px) {
    .main__container {
        width: 1194px;
    }
}
@media (max-width: 1199px) {
    .main__container {
        padding: 0 20px;
        background-size: contain;
    }
}
.main__title {
    padding: 22px 13px 17px;
    font-family: 'helveticaneuecyr_black';
    font-size: 40px;
    text-align: center;
}
@media (max-width: 768px) {
    .main__title {
        font-size: 24px;
        padding: 0 0 17px;
    }
}
.main__img img {
   display: block;
   width: 100%;
}
