.item {
    padding: 25px 20Px;
    background: #FFFFFF;
    border-radius: 5px;
    font-size: 14px;
    margin: 35px 15px 0;
    height: 300px;
    display: flex;
    flex-direction: column;
    
}
.text {
    height: 100%;
}
.name {
    font-size: 24px;
}

.payInHour {
    font-weight: 550;
    font-size: 16px;
    padding-top: 20px;
}

.time,
.city,
.house,
.vacancy {
    padding-top: 14px;
}



.button {
    margin-top: 5px;
    cursor: pointer;
    background-color: #FFEF62;
    font-family: 'helveticaneuecyr_black';
    font-size: 14px;
    border-radius: 5px;
    border: none;
    padding: 12px 0;
    width: 100%;
}